import React from "react";

import '../styles/description-style.css';

import Title from "./checkbox-label";
import ButtonOnDescription from "./button-on-description";
import * as axios from "axios";

class Description extends React.Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div>
                <img className='description-pic' src={this.props.image}/>
                <div className='data'>
                    <p className='data-name'>{this.props.name}</p>
                    <p className='data-time'>{this.props.date}</p>
                    <p className='data-cab'>{this.props.place}</p>
                    <p className='data-member'>{this.props.member}</p>
                    <Title/>
                    <div className='data-button'>
                        <ButtonOnDescription/>
                    </div>
                </div>
                <div className='about'>
                    <h1 className='about-event'>О событии</h1>
                    <div className='about-block'>
                        <p>
                            {this.props.description}
                        </p>
                    </div>
                </div>
            </div>
        )
    }
}

export default Description;